import React, { Component, Fragment } from 'react'
import Routes from '../routes/Routes';
import Navbar from './navbar/Navbar';
import './Layout.scss'

class Layout extends Component {
    render() {
        return (
            <Fragment>
                <Navbar />
                <div className="main container-fluid">
                    <Routes />
                </div>
            </Fragment>
        )
    }
}

export default Layout;